.btn {
  padding: 20px;
}
.btn, .btn:link, .btn:visited {
  position: relative;
  overflow: hidden;
  display: inline-block;
  width: 200px;
  border-radius: 100px;
  background-color: #E1D4C9;
  text-align: center;
  color: #403F3D;
  cursor: pointer;
  transition: padding-right 0.4s ease;
}
.btn__icon-logo {
  position: absolute;
  right: 60px;
  display: inline-block;
  margin-left: 8px;
  transform: scale(0);
  transition: transform 0.5s ease;
}
.btn__icon-logo > img {
  width: 20px;
  height: 20px;
}
.btn:hover {
  padding-right: 60px;
}
.btn:hover .btn__icon-logo {
  transform: scale(1);
}

.section_title {
  margin: 20px auto;
  position: relative;
}

