.column_section {
  margin: 100px 0;
  justify-content: space-between;
}
.column_section img {
  width: 660px;
  height: 660px;
  border-radius: 40px;
}
.column_section .section-content {
  flex-direction: column;
  width: 660px;
  height: 660px;
}
.column_section .section-content .section-content_title {
  margin: 20px 0;
  text-align: center;
  position: relative;
}
.column_section .section-content .section-content_title::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  width: 120px;
  height: 60px;
  border-bottom: 3px solid #665F55;
}
.column_section .section-content .section-content_caption {
  margin: 40px 0;
  text-align: center;
}
.column_section .section-content .section-content_medium {
  margin: 20px 30px;
  text-indent: 36px;
  text-align: justify;
}

header {
  padding: 20px 0;
}

header .wrapper .wrapper-content {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  width: 100px;
  height: 60px;
  flex-shrink: 0;
}
.header__logo a > img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.nav_wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.nav_wrapper .header__nav {
  flex: 1;
  /*
      @include media-tablet {
        position: absolute;
        top: 90px;
        right: -$slide-menu-width;

        width: $slide-menu-width;
        padding: 10px 107px 100px;

        transition: $ease-moving;
        z-index: 5;

        &.is_open {
          right: 0;
        }
      }

      @include media-mobile {
        position: absolute;
        top: 90px;
        right: -$slide-menu-width;

        width: $slide-menu-width;
        padding: 10px 107px 100px;

        transition: $ease-moving;
        z-index: 5;

        &.is_open {
          right: 0;
        }
      }  */
}
.nav_wrapper .header__nav .header__nav_list {
  display: flex;
  justify-content: flex-end;
  gap: 40px;
  /*
  @include media-tablet {
    display: flex;
    flex-direction: column;
    gap: 30px;

    text-align: center;
  }
  @include media-mobile {
    display: flex;
    flex-direction: column;
    gap: 30px;

    text-align: center;
  }*/
}
.nav_wrapper .header__nav .header__nav_list .nav-item a {
  position: relative;
}
.nav_wrapper .header__nav .header__nav_list .nav-item a.nav-item_link {
  color: #403F3D;
}
.nav_wrapper .header__nav .header__nav_list .nav-item a.nav-item_link::after {
  content: "";
  position: absolute;
  left: 0;
  display: inline-block;
  height: 1.5em;
  width: 100%;
  border-bottom: 2px solid;
  opacity: 0;
  transition: opacity 0.35s, transform 0.35s;
  transform: scale(0, 1);
}
.nav_wrapper .header__nav .header__nav_list .nav-item a.nav-item_link:hover::after {
  opacity: 1;
  transform: scale(1);
}

.enjoy__bg {
  position: relative;
  width: 100%;
  height: 644px;
  background-image: url("assets/images/enjoy.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  border-radius: 40px;
  /*
    @include media-tablet {
      background-image: url('./images/welcome-tablet.jpg');
    }
    @include media-small {
      background-image: url('./images/welcome-tablet.jpg');
    }*/
}
.enjoy__bg .enjoy__wrapper {
  position: absolute;
  z-index: 1;
  width: 730px;
  padding: 200px 0 0 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  align-self: stretch;
  gap: 40px;
}
.enjoy__bg .enjoy__wrapper h1 {
  color: #E1D4C9;
}
.enjoy__bg .enjoy__wrapper h1 span.accent {
  color: #B0907A;
}
.enjoy__bg .enjoy__wrapper p {
  color: #E1D4C9;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 130%;
}

